@import '../../mixin'; // responsive web

.listItem{
    width: 95%;
    height: auto;
    margin: 0 2.5%;
    cursor:pointer;
    color: #fff;
    position:relative;
    img{
        width:100%;
        height: 100%;
        object-fit: cover;
    }
    .ranking{
        position:absolute;
        top:0;
        left: 0;
        background-color: var(--red);
        color: #fff;
        font-weight: 900;
        font-size: #{$font-default};
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}



@include mobile {
    .listItem{
        .ranking{
            font-size: 1.5rem;
            width: 2.5em;
            height: 2.5em;
        }
    }
}

@include tablet {    
    .listItem{
        .ranking{
            font-size: #{$font-medium};
            width: 45px;
            height:45px;
        }
    }
}

